<template>
  <div class="upload-container">
    <div class="game-intro">
      <div class="title">{{lvTitle}}</div>
      <div class="desc" v-html='lvText'></div>
    </div>
    <div class="op-area">
      <div class="step-two" @click="handleLink">
        <!-- <div class="title">
          上传参赛方案格式要求：PDF / PP
        </div> -->
        <div class="upload">
          <img src="@/common/images/icon_edit.png" alt="">
        </div>
        <div class="desc">立即答题</div>
      </div>
      <div class="step-two">
        <div class="upload" @click="handleLinkUploader">
          <img src="@/common/images/upload.png" alt="">
        </div>
        <div class="desc" >上传方案</div>
      </div>
    </div>
    
  </div>
</template>
<script>
import storage from 'good-storage';
export default {
  props: {
    taskType: {
      type: String,
      default: 'factory'
    },
    lvTitle: {
      type: String,
      default: '',
    },
    lvText: {
      type: String,
      default:''
    }
  },
  data() {
    return {
      
    }
  },
  methods: {
    handleLink() {
      let type = this.taskType;
      const token = storage.get('token');
      const uinfo = JSON.parse(storage.get('uinfo'));
      if(type == 'safe') {
        window.open(`http://ml.antiycloud.com?sessionid=${token}&username=${uinfo.nick}`);
      } else if(type === 'factory'){
        window.open(`https://aicompetition.lenovomm.cn/workplace/index/?sessionid=${token}&username=${uinfo.nick}`);
      } else {
        window.open('/#/jd')
      }
    },
    handleLinkUploader() {
      this.$emit('link')
    }
  },
  destroyed() {
    Bus.$off('fileAdded');
    Bus.$off('fileSuccess');
  },
}
</script>
